body {
  /*overflow: hidden;*/

  background: #070707;
  height: 100%;
}

#root {
  position: relative;
  min-height: 100vh;
}

.App {
  color: #fff;
  font-size: 24px;
  height: 100%;

  padding-bottom: 80px;
}

.table_holder {
  margin: 20px;
  padding: 20px;
  border-radius: 8px;

  width: 90%;
  height: 100%;
  background-color: #ffe81a;
}

.table_wrapper {
  min-width: auto;
  height: 100%;
  border-radius: 5px;
  background-color: #222;

  padding: 20px;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.clist table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: auto;

  font-size: 16px;
}

.clist thead {
  color: #fff;
}

.clist tbody {
  color: #fff;
}

.clist th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

.clist td {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

.bar {
  margin-top: 20px;
  height: 55px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;

  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 47px;

  color: #ffffff;
}

.bar .logo {
  height: 55px;

  display: flex;
  align-items: center;
}

.bar .logo .icon {
  width: 55px;
  height: 55px;

  background-image: url("images/logo.png");
  background-position: center;
  background-size: contain;
}

.bar .logo .text {
  margin-left: 5px;
}

a {
  color: white;
  text-decoration: none;

  &:focus,
  &:hover,
  &:visited,
  &:link,
  &:active {
    text-decoration: none;
  }
}

a:visited {
  color: white;
}

a:hover {
  color: #999;
}

.active {
  text-decoration: underline;
}

.inactive {
  text-decoration: none;
}

.bar .login_btn {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.bar .login_btn .icon {
  width: 30px;
  height: 30px;

  background-image: url("images/login.png");
  background-position: center;
  background-size: contain;
}

.bar .login_btn .text {
  margin-left: 5px;
}

.content {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.content .left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.content .left .figure {
  margin-left: 60px;
}

.content .left .figure .polygon {
  width: 500px;
  height: 400px;

  fill: #ffe81a;
  stroke: #ffe81a;
  stroke-width: 20;
  stroke-linejoin: round;
  filter: drop-shadow(0px 0px 10px #ffe81a);
}

.content .left .description {
  color: #fff;
  font-size: 40px;
  padding-left: 40px;
}

.content .left .description .blogger {
  padding-left: 60px;
  padding-top: 60px;
}

.content .left .description .blogger {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.content .left .description .blogger .imgholder {
  width: 200px;
  height: 200px;

  background-image: url("images/blogger.png");
  background-position: center;
  background-size: contain;
}

.content .left .description .blogger .text {
  font-size: 32px;
  width: 500px;
  text-align: center;
}

.todolist {
  padding-top: 20px;

  font-size: 28px;
}

.todolist .list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;

  margin-top: 20px;
}

.todolist .elem {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.todolist .imgholder {
  width: 50px;
  height: 50px;
  margin: 5px;
}

.todolist .text {
  padding-left: 10px;
}

#ad {
  background-image: url("images/ad.png");
  background-position: center;
  background-size: contain;
}

#conf {
  background-image: url("images/music-festival.png");
  background-position: center;
  background-size: contain;
}

#colab {
  background-image: url("images/support.png");
  background-position: center;
  background-size: contain;
}

#money {
  background-image: url("images/banknote.png");
  background-position: center;
  background-size: contain;
}

.content .left .description .bottomtext {
  font-size: 28px;

  margin: 40px;
}

.content .right .backgroundgradient {
  width: 400px;
  height: 1250px;

  margin-right: 40px;

  clip-path: url(#clipping);

  background: linear-gradient(0, #ffe819 10%, rgba(255, 221, 133, 0) 95%);
}

#yt {
  width: 100px;
  height: 100px;

  position: relative;
  left: 70px;
  top: 80px;

  background-image: url("images/youtube.png");
  background-position: center;
  background-size: contain;

  transform: rotate(25deg);
}

#twitch {
  width: 100px;
  height: 100px;

  position: relative;
  left: 200px;
  top: 70px;

  background-image: url("images/twitch.png");
  background-position: center;
  background-size: contain;

  transform: rotate(8deg);
}

#ins {
  width: 100px;
  height: 100px;

  position: relative;
  left: 100px;
  top: 70px;

  background-image: url("images/instagram.png");
  background-position: center;
  background-size: contain;

  transform: rotate(-20deg);
}

#tt {
  width: 100px;
  height: 100px;

  position: relative;
  left: 180px;
  top: 70px;

  background-image: url("images/tiktok.png");
  background-position: center;
  background-size: contain;

  transform: rotate(15deg);
}

.footer {
  position: absolute;
  bottom: 0;
  margin-top: -80px;
  width: 100%;
  height: 80px;
  background: #ffe819;

  font-size: 20px;
  font-style: oblique;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.footer .sometext {
  margin: 10px;
}

.footer .support {
  margin: 10px;
}

.error_msg {
  color: #f00;
  font-size: 16px;
}

.link {
  color: #444;
}
